import { NzMessageService } from 'ng-zorro-antd/message';
import { NzModalService } from 'ng-zorro-antd/modal';
import { ChangeDetectorRef, Component, OnInit, HostListener } from '@angular/core';
import { ActivatedRouteSnapshot, Router, RouterStateSnapshot, UrlTree } from '@angular/router';
import { VhAlgorithm, VhAuth, VhDisplayLEDService, VhQuery, VhQuerySales } from 'ionic-vhframeworks'; 
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import {  VhSaleInvoices } from 'src/app/sales/interface/vh-sale-invoice';
import { LanguageService } from 'src/app/sales/services/language.service';
import { FunctionService } from 'src/app/sales/services/function.service'; 
import { Platform } from '@ionic/angular';
import { NzNotificationService } from 'ng-zorro-antd/notification';

@Component({
  selector: 'app-exchange-order-payment-card',
  templateUrl: './exchange-order-payment-card.component.html',
  styleUrls: ['./exchange-order-payment-card.component.scss']
})
export class ExchangeOrderPaymentCardComponent implements OnInit {
  useBranch: boolean = this.functionService.checkUsingBranch()
  public searchValue: string = ''
  public invoiceShow: any[] = []
  public invoiceToday: any[];
  tableHeight: string;
  public date: Date[] = [new Date(), new Date()];

  public invoiceInfo: any;
  public invoice: any // bill được chọn để xem chi tiết

  constructor(
    private vhQuery: VhQuery,
    private router: Router,
    public vhQuerySales: VhQuerySales,
    public vhAlgorithm: VhAlgorithm,
    public vhComponent: VhComponent,
    private languageService: LanguageService,
    public vhAuth: VhAuth,
    private cdRef: ChangeDetectorRef,
    private functionService: FunctionService,
    private notification: NzNotificationService,
    private plaform: Platform,
    private vhDisplayLEDService: VhDisplayLEDService,
    private fncService: FunctionService
  ) {
    this.date[0].setHours(0, 0, 0, 0)
    this.date[1].setHours(23, 59, 59, 59)
    if(this.router.getCurrentNavigation().extras.state){
      this.invoiceToday = this.router.getCurrentNavigation().extras.state.invoiceToday
      this.invoiceShow =  this.invoiceToday 
    }
  }

  ngOnInit() {

  }


  changeTime(event) {
    if (new Date(event[1].setHours(0, 0, 0, 0)).getTime() - new Date(event[0].setHours(0, 0, 0, 0)).getTime() <= this.fncService.checkDateReport() * 86000000) {
      //this.getTransferList();
    } else {
      this.vhComponent.showToast(4000, this.languageService.translate("The time limit to view is") + " " + this.fncService.checkDateReport() + " " + this.languageService.translate("days"), "alert-toast")
      this.date = [new Date(event[0]), new Date(event[0].getTime() + this.fncService.checkDateReport() * 86000000)]
    }
  }


  getInvoiceToday() {
    this.vhComponent.showLoading('').then(() => {
      this.vhQuerySales.getBills_byFields( { date: { $gte: new Date(this.date[0].setHours(0, 0, 0)), $lte: new Date(this.date[1].setHours(23, 59, 59, 59)) }, bill_type: { $in: [53] }, id_branch: { $eq: this.vhQuerySales.getDefaultBranch()._id } }, {})
        .then((invoice: any[]) => {
          
          let promise = new Array();
          for (let i = 0; i < invoice.length; i++) {
            promise[i] = this.vhQuerySales.getCustomer(invoice[i].id_customer)
          }
          this.vhQuery.asyncPromiseAll(promise).then((array) => {
            this.invoiceToday = invoice.map((item,index) => { 
              return { ...item, customer: array[index],  sum_price: item.total * (1 + item.tax / 100),   }
            })
            this.searchBill(this.searchValue)
          }, error => { })
          this.vhComponent.hideLoading(0);

        })
    })
  }
  searchBill(value: string) {
    if (value.length) {
      let val: string = this.vhAlgorithm.changeAlias(value.trim().toLowerCase())
      this.invoiceShow = this.vhAlgorithm.searchList(val, this.invoiceToday, ['bill_code', 'partner_name', 'employee_name', 'payment_name', 'status', 'phone'])
    } else this.invoiceShow = [...this.invoiceToday]
    

  }
  /* -----Pagination----- */
  onCurrentPageDataChange(value) {
  }

  goToDetail(bill) {
    
    
    if (this.vhAuth.checkMyPermission("sales_enable_view_bill_today")) {
      this.router.navigate(["/bill-web-app/exchange-order-payment-card/detail"], { state: { bill: bill, invoiceToday :  this.invoiceToday , customer : bill.customer} })
    }
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }
  goToCart() {

    this.router.navigate(['/sales/sales-desktop/add'])
  }
  public renderAddress(partner) {
    return (partner.address ? partner.address : '') +
      (partner.district ? ', ' + partner.district : '') +
      (partner.province ? ', ' + partner.province : '') +
      (partner.country ? ', ' + partner.country : '')
  }
  ngAfterViewChecked() {
    if (document.querySelector("#sales-invoice-today") && document.querySelector(".ant-table-thead") && document.querySelector(".sales-invoice-today-header") && document.querySelector(".ant-table-pagination")) {
      this.tableHeight = document.querySelector("#sales-invoice-today").clientHeight - document.querySelector(".ant-table-thead").clientHeight
        - document.querySelector(".sales-invoice-today-header").clientHeight - 110 + "px";
    }
    this.cdRef.detectChanges();
  } 

  deleteBill() {
    let bill: any = { ...this.invoice }
    this.invoiceShow = this.invoiceShow.filter(item => item._id != bill._id)
  }


  /** hàm sort cho các cột */
  dateCol = false;
  bill_codeCol = false;
  partner_nameCol = false;
  payment_nameCol = false;
  name_employeeCol = false;
  sum_priceCol = false;
  /** Hàm thực hiện sắp xếp theo collection
   *
   * @param colName       // tên cột muôn sắp xếp
   */
  sortTable(colName) {
    switch (colName) {
      case 'date':
        if (this.dateCol) {
          this.invoiceShow = this.vhAlgorithm.sortDatebyASC([...this.invoiceShow], colName);
        } else {
          this.invoiceShow = this.vhAlgorithm.sortDatebyDESC([...this.invoiceShow], colName);
        }
        break;
      case 'bill_code':
        if (this.bill_codeCol) {
          this.invoiceShow = this.vhAlgorithm.sortVietnamesebyASC([...this.invoiceShow], colName);
        } else {
          this.invoiceShow = this.vhAlgorithm.sortVietnamesebyDESC([...this.invoiceShow], colName);
        }
        break;
      case 'partner_name':
        if (this.partner_nameCol) {
          this.invoiceShow = this.vhAlgorithm.sortVietnamesebyASC([...this.invoiceShow], colName);
        } else {
          this.invoiceShow = this.vhAlgorithm.sortVietnamesebyDESC([...this.invoiceShow], colName);
        }
        break;
      case 'payment_name':
        if (this.payment_nameCol) {
          this.invoiceShow = this.vhAlgorithm.sortVietnamesebyASC([...this.invoiceShow], colName);
        } else {
          this.invoiceShow = this.vhAlgorithm.sortVietnamesebyDESC([...this.invoiceShow], colName);
        }
        break;

      case 'sum_price':
        if (this.sum_priceCol) {
          this.invoiceShow = this.vhAlgorithm.sortNumberbyASC([...this.invoiceShow], colName);
        } else {
          this.invoiceShow = this.vhAlgorithm.sortNumberbyDESC([...this.invoiceShow], colName);
        }
        break;
    }
  }

}
