import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { TranslateModule } from '@ngx-translate/core';
import { IonicModule } from '@ionic/angular';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module'; 
import { HeaderAccountInfoModule } from '../header-account-info/header-account-info.module';
import { DetailComponent } from './detail/detail.component';
import { ExchangeOrderPaymentCardComponent } from './exchange-order-payment-card.component';

const routes: Routes = [
  {
    path: '',
    component: ExchangeOrderPaymentCardComponent,
  },
  {
    path: 'detail',
    component: DetailComponent,
  }
]


@NgModule({
  declarations: [ExchangeOrderPaymentCardComponent,DetailComponent],
  imports: [
    CommonModule,
    ReactiveFormsModule,
    FormsModule,
    NgZorroAntModule,
    TranslateModule,
    IonicModule,
    RouterModule.forChild(routes),
    HeaderAccountInfoModule
  ]
})
export class ExchangeOrderPaymentCardModule { }
