
import { ExchangeVoucherA4Component } from 'src/app/sales/components/print/exchange-voucher/exchange-voucher-a4/exchange-voucher-a4.component';
import { ExchangeVoucherA5Component } from 'src/app/sales/components/print/exchange-voucher/exchange-voucher-a5/exchange-voucher-a5.component';
import { ExchangeVoucherK57Col3Component } from 'src/app/sales/components/print/exchange-voucher/exchange-voucher-k57-col3/exchange-voucher-k57-col3.component';
import { ExchangeVoucherK80Col3Component } from 'src/app/sales/components/print/exchange-voucher/exchange-voucher-k80-col3/exchange-voucher-k80-col3.component';
import { ExchangeVoucherK80Col4Component } from 'src/app/sales/components/print/exchange-voucher/exchange-voucher-k80-col4/exchange-voucher-k80-col4.component';
import { ActivatedRouteSnapshot, Router } from '@angular/router';
import { ChangeDetectorRef, Component, ElementRef, HostListener, OnInit, ViewChild } from '@angular/core';
import { VhAlgorithm, VhAuth, VhQuery, VhQuerySales } from 'ionic-vhframeworks';

import { NzModalService } from 'ng-zorro-antd/modal';
import { FunctionService } from 'src/app/sales/services/function.service';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { LanguageService } from 'src/app/sales/services/language.service'; 
import { VhBillType51DetailAdd, VhBillType51DetailEdit, VhBillType51Edit } from 'src/app/sales/components/bill-type/bill-type-51/bill-type51-interface';
@Component({
  selector: 'app-detail',
  templateUrl: './detail.component.html',
  styleUrls: ['./detail.component.scss']
})
export class DetailComponent implements OnInit {
  listShow = []
  public root: any; // biến nhận state truyền qua
  public invoice: VhBillType51Edit;
  invoiceDetail: VhBillType51DetailEdit[];
  public employee: any = {};
  public pickerTime = new Date();
  public customer: any;
  public checkPrinter: boolean = false;
  public closerBrower: any;
  public id_product_delete: string = '';
  customer_online: any;
  constructor(
    private router: Router,
    private vhComponent: VhComponent,
    private languageService: LanguageService,
    public vhQuerySales: VhQuerySales,
    public vhAlgorithm: VhAlgorithm,
    public vhAuth: VhAuth,
    private nzModalService: NzModalService,
    private fncService: FunctionService,
    private cdf: ChangeDetectorRef,
    private vhQuery: VhQuery
  ) {
    this.root = this.router.getCurrentNavigation().extras.state;
    this.customer_online = this.root.customer
    this.getDataInvoice(this.root.bill);
  }

  ngOnInit(): void {

  }

  private getDataInvoice(bill) {
    this.vhComponent.showLoading("", "transparent-loading").then(() => {
      Promise.all([
        this.vhQuerySales.getBill_details_byId_bill(bill._id)
      ]).then(([invoiceDetail]: any) => {
        
        if (!invoiceDetail.length)
          this.init(bill, [])
        else {
          // invoiceDetail = this.vhAlgorithm.sortVietnamesebyASC(invoiceDetail, 'name');
          let promise = []
          for (let i in invoiceDetail) {
            promise[i] = this.vhQuerySales.getPaymentCard(invoiceDetail[i].id_payment_card)
          }
          Promise.all(promise).then((array) => {
            
            
            invoiceDetail = invoiceDetail.map((e, index) => {
              return { ...e, name :  array[index].name, products : e.products.map(pro =>{
                let detailproduct  = this.vhQuerySales.getlocalDetailProduct(pro.id_subproduct ? pro.id_subproduct : pro.id_product)
                return {...pro, units : detailproduct.units, name : detailproduct.name}
              }) }
            })
            this.init(bill, invoiceDetail);
            this.listShow = invoiceDetail;
            

          })

        }
      })
    })
  }

  init(invoice   , invoiceDetail ) {
    this.vhComponent.hideLoading(0)
    this.invoice = invoice;
    this.invoiceDetail = invoiceDetail
    if (invoice && invoiceDetail) {
      this.invoice.id_employee = (this.vhAuth.getUser()._id)
      this.invoice.date = (new Date())
      this.note = this.invoice.note;
      this.pickerTime = new Date(this.invoice.date);
      this.employee = this.vhAuth.getlocalEmployee(this.invoice.id_employee);

    }
  }
  ngAfterViewInit() {
    let printer = this.vhQuerySales.getLocalPrintPage('page_desktop_sales_voucher')
    if (printer) this.checkPrinter = printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)].enable;


  }

  ngAfterViewChecked(): void {
    this.cdf.detectChanges()
  }


  // chuổi danh sách sản phẩm
  getStringName(products: any[]) {
    let name = ''
    products.forEach(element => {
      let product = this.vhQuerySales.getlocalDetailProduct(element.id_subproduct ? element.id_subproduct : element.id_product)
      name += '- ' + element.quantity + 'x ' + product.name + ' (' + this.vhQuerySales.getUnit_byRatio(product.units, element.ratio).unit + ') \n';
    });
    return name
  }

  goBack() {
    this.router.navigate(['/sales/bill-web-app/exchange-order-payment-card']);
  }

  // -------set employee-------
  employees = this.vhAuth.getlocalEmployees();
  closeAndReceiveEmployee(event) {
    if (event) {
      this.invoice.id_employee  = (event);
    }
  }
  setPickerTime(event) {
    if (new Date(event).getTime() < new Date(this.vhAuth.getStore().registrationdate).getTime()) {
      this.vhComponent.alertMessageDesktop("warning", `${this.languageService.translate("You can only create or edit the invoice from the registration date (")} ${this.fncService.formatDate(this.vhAuth.getStore().registrationdate)} ${")"}`, 5000)
      this.pickerTime = new Date(this.invoice.date)
      this.invoice.date = (new Date(this.pickerTime));
    }
    else this.vhQuerySales.getValidityDate(new Date(event)).then((validitydate: any) => {
      if (validitydate == null) {
        this.invoice.date = (event);
      }
      else {
        this.pickerTime = new Date(validitydate)
        this.invoice.date = (new Date(this.pickerTime));
        this.vhComponent.alertMessageDesktop("warning", `${this.languageService.translate("You can only create or edit the invoice from the date")} ${this.fncService.formatDate(validitydate)}`, 5000)
      }
    })
  }

  payInvoice(bill_type: 51, print?) {
    
    if (this.checkBillDetailSave()) {
      if (this.vhAuth.checkMyPermission("sales_enable_payment")) {
        if (print) {
          if (this.checkPrinter) {
            this.vhComponent.showLoading("", "transparent-loading").then(() => {
              this.createOrUpdate().then(() => {
                
                this.vhComponent.hideLoading(0).then(() => {
                  this.vhComponent.showToast(5000,
                    `${this.languageService.translate("Hóa đơn đổi thẻ lấy hàng hóa")} ${ this.invoice.bill_code} ${this.languageService.translate("has been created successfully")}`, "success-toast"
                  )
                  this.handlePrint()
                })
              })
            })
          }
          else {
            this.nzModalService.confirm({
              nzTitle: '<i>' + this.languageService.translate("Function") + '</i>',
              nzContent: '<b>' + this.languageService.translate("Please open the function in") + ' ' + '<i>' + '"' + this.languageService.translate("Sales page settings") + '"' + '</i>' + ' ' + ' -> ' + '<i>' + '"' + this.languageService.translate('Print page') + '"' + '</i>' + '</b>',
              nzIconType: "info-circle",
              nzCancelText: null,
              nzOnOk: () => { }
            })
          }
        }
        else {
          this.vhComponent.showLoading("", "transparent-loading").then(() => {
            this.createOrUpdate().then(() => {
              
              this.vhComponent.hideLoading(0).then(() => {
                this.vhComponent.showToast(5000,
                  `${this.languageService.translate("Hóa đơn đổi thẻ lấy hàng hóa")} ${ this.invoice.bill_code} ${this.languageService.translate("has been created successfully")}`, "success-toast"
                )
                this.goBack()
              })
            })
          })
        }
      }
      else this.vhComponent.showToast(5000, this.languageService.translate('You do not have this rights'), "alert-toast")
    }
    else this.vhComponent.alertMessageDesktop("error", this.languageService.translate('Please select the lot number for the products in the voucher'))
  }

  checkBillDetailSave() {
    for (let i of this.invoiceDetail) {
      if (i.products.find(item => (item.lots && !item.id_lotproduct) || (item.combos && item.combos.find(e => e.lots && !e.id_lotproduct)))) return false;
    }
    return true
  }


  createOrUpdate() {
    return new Promise(resolve => {
      let invoice = { ...this.invoice }
      this.vhQuerySales.updateBill_Billdetail(this.invoice._id, invoice, this.getCreateUpdateInvoiceDetail())
        .then(() => {
          resolve(this.vhQuerySales.saveBill_Billdetail(this.invoice._id,51))
        })
    })
  }
  getCreateUpdateInvoiceDetail(): VhBillType51DetailAdd[] {
    return this.invoiceDetail.map((item) => {
      let detail: VhBillType51DetailAdd = {
        id_payment_card: item.id_payment_card,
        id_payment_card_code: item.id_payment_card_code,
        quantity: 1,
        pctype: 1,
        ratio: 1,
        price: item.price,
        products: item.products.map(pro => {
          let data = {
            id_product: pro.id_product,
            ptype: pro.ptype,
            ratio: pro.ratio,
            quantity: pro.quantity,
            price: 0,
          }
          if (pro.id_subproduct) data['id_subproduct'] = pro.id_subproduct
          if (pro.combos) {
            data['combos'] = pro.combos.map(combo => {
              let product = {
                id_product: combo.id_product,
                price: combo.price,
                quantity: combo.quantity,
                ptype: combo.ptype,
                ratio: combo.ratio ? combo.ratio : 1,
              }
              if (combo.id_lotproduct) product['id_lotproduct'] = combo.id_lotproduct
              if (combo.id_subproduct) product['id_subproduct'] = combo.id_subproduct
              return product;
            })
          }
          return data
        }),
       
      };

      return detail;
    });
  }
  deleteInvoice() {
    this.vhComponent.showLoading('')
      .then(() => {
        this.vhQuerySales.deleteBill_Billdetail(this.invoice._id)
          .then((bool) => {
            if (bool) {
              this.vhComponent.hideLoading(0)
              
              this.goBack()
            } else {
              
            }
          }, (error: any) => {
            
          })
      })

  }
   /**
   * lấy thông tin dữ liệu trang in
   * @returns Promise<any>
   * @example this.checkPrint()
   */
  checkPrint() {
    return new Promise((resolve, rejects) => {
      let printer = this.vhQuerySales.getlocalPrinters().find(item => item.name == "DESKTOP")
      if (printer && printer.voucher.enable) resolve(printer.voucher)
      else resolve(false)
      if (!printer) rejects(false)
    })
  }

  /**
   * trả về component của trang in tương ứng
   * @example this.renderPrintPage('print_size_a4')
   * @returns component
   */
  private renderPrintPage(type: 'print_size_a4' | 'print_size_a5' | 'print_size_k80_4c' | 'print_size_k80_3c' | 'print_size_k57_3c') {
    switch (type) {
      case 'print_size_k57_3c': return ExchangeVoucherK57Col3Component
      case 'print_size_k80_3c': return ExchangeVoucherK80Col3Component
      case 'print_size_k80_4c': return ExchangeVoucherK80Col4Component
      case 'print_size_a5': return ExchangeVoucherA5Component
      case 'print_size_a4': return ExchangeVoucherA4Component
      default: return ExchangeVoucherA4Component
    }
  }


  handlePrint() {
    let print = this.vhQuerySales.getLocalPrintPage('page_desktop_sales_voucher')
    if (print && print.print_sizes[print.print_sizes.findIndex(e => e._id == print.print_size_default)].enable) {
      let printer = print.print_sizes[print.print_sizes.findIndex(e => e._id == print.print_size_default)]
      if (printer) {
        let type = printer._id;
        this.vhComponent.showModal(this.renderPrintPage(type), { printer: printer, invoice: this.invoice, invoice_detail : this.listShow }, false, false, `modal-print-${type}`).then((modal) => {
          modal.onWillDismiss().then(() => this.goBack())
        })
      }
    }
  }
  note: string;
  edit_note() {
    if (this.note) {
      this.invoice.note = (this.note)
    }
  }
  getUnit(units: any[], ratio) {
    return this.vhQuerySales.getUnit_byRatio(units, ratio).unit
  }
  getLotNumber(lots, _id) {
    let lot = lots.find(item => item._id == _id);
    return lot.lot_number
  }
  tempDetail: any = {}
  show_modal_select_lots_combo = false;
  handleChooseLotCombo() {
    let check = false;
    for (let i of this.tempDetail) {
      if (i.ptype == 1 && !i.id_lotproduct) check = true;
      if (i.ptype == 5 && i.combos.filter(item => item.lots && !item.id_lotproduct).length) check = true;
    }
    if (check)
      this.vhComponent.alertMessageDesktop("warning", this.languageService.translate("Please select the lot for the product!"))
    else {
      this.show_modal_select_lots_combo = false;
      // this.closeSearchProductList(this.tempDetail)
    }
  }

  deleteGoods(position) {
    if (this.listShow.length == 1) {
      this.listShow = []
    } else {
      this.listShow.splice(position, 1);
    }
  }
}
